import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import '../assets/css/CartCompo.css'


const CartBody = () => {

  return (
    <div className="CartCompoS">

        <div className='cartbody'>
            <div className='cartAddedItem'>
                <div className='singleItem'>
                    <div className='ImgClass'>
                        <img src="https://dummyimage.com/650x500/c4c4c4/000000"/>
                    </div>
                    <div className='flx-t'>
                        <span className='boldtxt'>Products</span>
                        <span className='tag'>JBL Boombox 3 - Portable Bluetooth Waterproof Speaker</span>
                    </div>
                    <div className='flx-t'>
                        <span className='boldtxt'>Products</span>
                        <span className='price'>LKR 23,000</span>
                    </div>
                    <div className='flx-t'>
                        <span className='boldtxt'>Quantity</span>
                        <input type="number" defaultValue="01" placeholder='01'/>
                    </div>
                    <div className='flx-t subT'>
                        <span className='boldtxt'>Sub Total</span>
                        <span className='price'>LKR 23,000</span>
                    </div>

                    <div className='closeIco'>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none">
                            <path d="M8.00047 7.05767L11.3003 3.75781L12.2431 4.70062L8.94327 8.00047L12.2431 11.3003L11.3003 12.2431L8.00047 8.94327L4.70062 12.2431L3.75781 11.3003L7.05767 8.00047L3.75781 4.70062L4.70062 3.75781L8.00047 7.05767Z" fill=""/>
                        </svg>
                    </div>
                </div>
            </div>
        </div>

        <div className='coupen-main'>
            <div className='left'>
                <input type="text" placeholder='Coupon code'/>
            </div>
            <div className='towBtns'>
                <Link to="/">
                    <button className="setBtn">Apply Coupon</button>
                </Link>
                <Link to="/">
                    <button className="setBtn">Update Cart</button>
                </Link>
            </div>
        </div>

        <div className='totDetail'>
            <span className='topic'>Cart Detail</span>

            <div className='wrapper'>
                <div className='flx-rw'>
                    <span className='bold'>Subtotal</span>
                    <span className='val'>LKR 23,000</span>
                </div>
                <div className='flx-rw'>
                    <span className='bold'>Delivery</span>
                    <span className='val'>LKR 0</span>
                </div>
                <div className='flx-rw'>
                    <span className='bold'>Total</span>
                    <span className='val'>LKR 23,000</span>
                </div>
                <Link to="/">
                    <button className='process'>Proceed to checkout</button>
                </Link>
            </div>
        </div>
    </div>
  );
};

export default CartBody;
