import React from 'react';
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import "../assets/css/Home.css";

function OurPartner () {
    
   const partnerSliderSettings = {
      dots: false,
      arrows: false,
      autoplay: true,
      infinite: true,
      pauseOnHover: true,
      slidesToShow: 4,
      slidesToScroll: 4,
      responsive: [
        {
          breakpoint: 992,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
          },
        },
      ],
    };

  return (
  <>
        <div className="logoLine container">
        <Slider {...partnerSliderSettings}> 
          <div className="brandslide">
            <img src={"https://dummyimage.com/150x80/c4c4c4/000000"} alt="" />
          </div>
          <div className="brandslide">
            <img src={"https://dummyimage.com/150x80/c4c4c4/000000"} alt="" />
          </div>
          <div className="brandslide">
            <img src={"https://dummyimage.com/150x80/c4c4c4/000000"} alt="" />
          </div>
          <div className="brandslide">
            <img src={"https://dummyimage.com/150x80/c4c4c4/000000"} alt="" />
          </div>
        </Slider>
      </div>
  </>
  );
};

export default OurPartner;
