// YourPage.js
import React,{useEffect , useState}from 'react';
import Breadcrumb from '../components/Breadcrumb';
import Footer from '../components/Footer'

import '../assets/css/checkP.css'
import { Link } from 'react-router-dom';

const CheckoutProcess = () => {
    //breadcrumb
  const breadcrumbPaths = [
    { label: 'Home', link: '/' },
    { label: 'Shop', link: '/shop' },
    { label: 'checkout' },
  ];

  const [acceptTerms, setAcceptTerms] = useState(false);
  const [selectedOption, setSelectedOption] = useState(null);
  const handleCheckboxChange = () => {
    setAcceptTerms(!acceptTerms);
  };
  const handleCheckboxChangePay = (option) => {
    setSelectedOption(option);   
  };
  const handleSubmit = (event) => {
    event.preventDefault();

    console.log('Submitted Data:', {
        firstName,
        lastName,
        comName,
        country,
        addLine1,
        addLine2,
        town,
        postCode,
        pnum,
        email,
        firstNameDefAdd,
        lastNameDefAdd,
        comNameDefAdd,
        countryDefAdd,
        addLine1DefAdd,
        addLine2DefAdd,
        townDefAdd,
        postCodeDefAdd,
        pnumDefAdd,
        emailDefAdd,
      });
  };


  //-------------------------------------------------------
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [comName, setComName] = useState('');
  const [country, setCountry] = useState('');
  const [addLine1, setAddLine1] = useState('');
  const [addLine2, setAddLine2] = useState('');
  const [town, setTown] = useState('');
  const [postCode, setPostCode] = useState('');
  const [pnum, setPnum] = useState('');
  const [email, setEmail] = useState('');


  const [firstNameDefAdd, setFirstNameDefAdd] = useState('');
  const [lastNameDefAdd, setLastNameDefAdd] = useState('');
  const [comNameDefAdd, setComNameDefAdd] = useState('');
  const [countryDefAdd, setCountryDefAdd] = useState('');
  const [addLine1DefAdd, setAddLine1DefAdd] = useState('');
  const [addLine2DefAdd, setAddLine2DefAdd] = useState('');
  const [townDefAdd, setTownDefAdd] = useState('');
  const [postCodeDefAdd, setPostCodeDefAdd] = useState('');
  const [pnumDefAdd, setPnumDefAdd] = useState('');
  const [emailDefAdd, setEmailDefAdd] = useState('');
  const [oderNoteDefAdd, setOrderNoteDefAdd] = useState('');

  const handleFirstNameChange = (event) => {
    setFirstName(event.target.value);
  };

  const handleLastNameChange = (event) => {
    setLastName(event.target.value);
  };

  const handleComNameChange = (event) => {
    setComName(event.target.value);
  };

  const handleCountryChange = (event) => {
    setCountry(event.target.value);
  };

  const handleAddLine1Change = (event) => {
    setAddLine1(event.target.value);
  };

  const handleAddLine2Change = (event) => {
    setAddLine2(event.target.value);
  };

  const handleTownChange = (event) => {
    setTown(event.target.value);
  };

  const handlePostCodeChange = (event) => {
    setPostCode(event.target.value);
  };

  const handlePnumChange = (event) => {
    setPnum(event.target.value);
  };

  const handleEmailChange = (event) => {
    setEmail(event.target.value);
  };


  const handleFirstNameDefAddChange = (event) => {
    setFirstNameDefAdd(event.target.value);
  };
  
  const handleLastNameDefAddChange = (event) => {
    setLastNameDefAdd(event.target.value);
  };
  
  const handleComNameDefAddChange = (event) => {
    setComNameDefAdd(event.target.value);
  };
  
  const handleCountryDefAddChange = (event) => {
    setCountryDefAdd(event.target.value);
  };
  
  const handleAddLine1DefAddChange = (event) => {
    setAddLine1DefAdd(event.target.value);
  };
  
  const handleAddLine2DefAddChange = (event) => {
    setAddLine2DefAdd(event.target.value);
  };
  
  const handleTownDefAddChange = (event) => {
    setTownDefAdd(event.target.value);
  };
  
  const handlePostCodeDefAddChange = (event) => {
    setPostCodeDefAdd(event.target.value);
  };
  
  const handlePnumDefAddChange = (event) => {
    setPnumDefAdd(event.target.value);
  };
  
  const handleEmailDefAddChange = (event) => {
    setEmailDefAdd(event.target.value);
  };

  const handleOrderNoteDefAdd = (event) => {
    setOrderNoteDefAdd(event.target.value);
  };


  
  //-------------------------------------------------------
  return (
    <div>
        <div className="page-wrapper container">
            <Breadcrumb paths={breadcrumbPaths} />
            <h4>Cart</h4>

            <div className='checkoutForm '>
                <span className='checkNAme'>Billing Address</span>

                <form onSubmit={handleSubmit}>
                    <div className='top-cont'>
                        <div className='flex-div'>
                            <label className='label'>
                                First Name *:
                                <input
                                type="text"
                                name="firstName"
                                value={firstName}
                                onChange={handleFirstNameChange}
                                placeholder='FirstName'
                                />
                            </label>
                            <label className='label'>
                                Last Name *:
                                <input
                                type="text"
                                name="firstName"
                                value={lastName}
                                onChange={handleLastNameChange }
                                placeholder='Last Name'
                                />
                            </label>
                        </div>
                        <div className='no-flex-div'>
                            <label className='label'>
                                Company Name (optional):
                                <input
                                type="text"
                                name="firstName"
                                value={comName}
                                onChange={handleComNameChange }
                                />
                            </label>
                        </div>
                        <div className='no-flex-div'>
                            <label className='label'>
                                Country / region *:
                                <select
                                name="country"
                                value={country}
                                onChange={handleCountryChange }
                                >
                                <option value="Select Country" disabled>
                                    Select Country
                                </option>
                                <option value="USA">United States</option>
                                <option value="Canada">Canada</option>
                                <option value="UK">United Kingdom</option>
                                {/* Add more country options as needed */}
                                </select>
                            </label>
                        </div>
                        <div className='no-flex-div'>
                            <label className='label'>
                                Street Address *:
                                <input
                                type="text"
                                name="firstName"
                                value={addLine1}
                                onChange={handleAddLine1Change }
                                placeholder='Street address'
                                />
                                <input
                                type="text"
                                name="firstName"
                                value={addLine2}
                                onChange={handleAddLine2Change }
                                placeholder='Town name'
                                />
                            </label>
                        </div>
                        <div className='flex-div'>
                            <label className='label'>
                                Town / City *:
                                <input
                                type="text"
                                name="firstName"
                                value={town}
                                onChange={handleTownChange }
                                placeholder='town'
                                />
                            </label>
                            <label className='label'>
                                Postal code / Zip Code *:
                                <input
                                type="text"
                                name="firstName"
                                value={postCode}
                                onChange={handlePostCodeChange }
                                placeholder='zip code'
                                />
                            </label>
                        </div>
                        <div className='flex-div'>
                            <label className='label'>
                                Phone number *:
                                <input
                                type="tel"
                                name="firstName"
                                value={pnum}
                                onChange={handlePnumChange }
                                placeholder='Phone Number'
                                />
                            </label>
                            <label className='label'>
                                Email Address *:
                                <input
                                type="email"
                                name="firstName"
                                value={email}
                                onChange={handleEmailChange }
                                placeholder='zip code'
                                />
                            </label>
                        </div>
                        <label className='label accept'>
                            <input
                                type="checkbox"
                                name="acceptTerms"
                                checked={acceptTerms}
                                onChange={handleCheckboxChange}
                                className='chSp'
                            />
                            Ship to a different address?
                            
                        </label>

                        {acceptTerms && (
                            <>

                            <div className='flex-div'>
                                    <label className='label'>
                                        First Name *:
                                        <input
                                        type="text"
                                        name="firstName"
                                        value={firstNameDefAdd}
                                        onChange={handleFirstNameDefAddChange }
                                        placeholder='FirstName'
                                        />
                                    </label>
                                    <label className='label'>
                                        Last Name *:
                                        <input
                                        type="text"
                                        name="firstName"
                                        value={lastNameDefAdd}
                                        onChange={handleLastNameDefAddChange }
                                        placeholder='Last Name'
                                        />
                                    </label>
                                </div>
                                <div className='no-flex-div'>
                                    <label className='label'>
                                        Company Name (optional):
                                        <input
                                        type="text"
                                        name="firstName"
                                        value={comNameDefAdd}
                                        onChange={handleComNameDefAddChange }
                                        />
                                    </label>
                                </div>
                                <div className='no-flex-div'>
                                    <label className='label'>
                                        Country / region *:
                                        <select
                                        name="country"
                                        value={countryDefAdd}
                                        onChange={handleCountryDefAddChange }
                                        >
                                        <option value="Select Country" disabled>
                                            Select Country
                                        </option>
                                        <option value="USA">United States</option>
                                        <option value="Canada">Canada</option>
                                        <option value="UK">United Kingdom</option>
                                        {/* Add more country options as needed */}
                                        </select>
                                    </label>
                                </div>
                                <div className='no-flex-div'>
                                    <label className='label'>
                                        Street Address *:
                                        <input
                                        type="text"
                                        name="firstName"
                                        value={addLine1DefAdd}
                                        onChange={handleAddLine1DefAddChange }
                                        placeholder='Street address'
                                        />
                                        <input
                                        type="text"
                                        name="firstName"
                                        value={addLine2DefAdd}
                                        onChange={handleAddLine2DefAddChange }
                                        placeholder='Town name'
                                        />
                                    </label>
                                </div>
                                <div className='flex-div'>
                                    <label className='label'>
                                        Town / City *:
                                        <input
                                        type="text"
                                        name="firstName"
                                        value={townDefAdd}
                                        onChange={handleTownDefAddChange }
                                        placeholder='town'
                                        />
                                    </label>
                                    <label className='label'>
                                        Postal code / Zip Code *:
                                        <input
                                        type="text"
                                        name="firstName"
                                        value={postCodeDefAdd}
                                        onChange={handlePostCodeDefAddChange }
                                        placeholder='zip code'
                                        />
                                    </label>
                                </div>
                                <div className='flex-div'>
                                    <label className='label'>
                                        Phone number *:
                                        <input
                                        type="tel"
                                        name="firstName"
                                        value={pnumDefAdd}
                                        onChange={handlePnumDefAddChange }
                                        placeholder='town'
                                        />
                                    </label>
                                    <label className='label'>
                                        Email Address *:
                                        <input
                                        type="email"
                                        name="firstName"
                                        value={emailDefAdd}
                                        onChange={handleEmailDefAddChange }
                                        placeholder='zip code'
                                        />
                                    </label>
                                </div>
                            </>
                        )}

                        <div className='no-flex-div'>
                            <label className='label'>
                                Order Notes (optional):
                                <textarea
                                    name="message"
                                    value={oderNoteDefAdd}
                                    onChange={handleOrderNoteDefAdd}
                                    />
                            </label>
                        </div>
                    </div>
                    
                    <div className='middleCont'>
                        <span className='topic'>Your Orders</span>

                        <div className='top-flx'>
                            <span className='lblSp'>Product</span>
                            <span className='lblSp'>Sub Total</span>
                        </div>

                        <div className='btm-flx'>
                            <div className='rw'>
                                <div className='sin-col'>
                                    <span className='smallLb'>JBL Boombox 3 - Portable Bluetooth Waterproof Speaker</span>
                                    <span className='colorLbl'>Color : black</span>    
                                </div>
                                <span className='lbl'>LKR 23,000</span>
                            </div>

                            <div className='rw'>
                                <span className='smallLbSP'>Delivery</span>
                                <span className='lblSp'>LKR 23,000</span>
                            </div>

                            <div className='rw'>
                                <span className='smallLbSP'>Subtotal</span>
                                <span className='lblSp'>LKR 23,000</span>
                            </div>

                            <div className='rw rw-last'>
                                <span className='smallLbSP'>Total</span>
                                <span className='lblSp totF'>LKR 23,000</span>
                            </div>
                        </div>
                    </div>

                    <div className="fianl-pay-option">
                        <div className="flex-rw">
                            <label>
                            <input
                                type="checkbox"
                                checked={selectedOption === 'cash'}
                                onChange={() => handleCheckboxChangePay('cash')}
                            />
                            Cash Upon Delivery
                            </label>
                            {selectedOption === 'cash' && (
                            <p>Pay with cash upon delivery only for order below LKR 20,000</p>
                            )}
                        </div>

                        <div className="flex-rw">
                            <label>
                            <input
                                type="checkbox"
                                checked={selectedOption === 'koko'}
                                onChange={() => handleCheckboxChangePay('koko')}
                            />
                            Pay with KOKO
                            </label>
                            {selectedOption === 'koko' && (
                            <p>Pay with cash upon delivery only for order below LKR 20,000</p>
                            )}
                        </div>
                    </div>

                        <span className='addTxt'>Your personal data will be used to process your order, support your experience throughout this website, and for other purposes described in our privacy policy.</span>
                        <Link to="/">
                            <button type="submit">Submit</button>
                        </Link>
                </form>
            </div>
        </div>
        
        <Footer/>
    </div>
  );
};

export default CheckoutProcess;
